      .availableTimes .labels span:nth-child(2) {
        left: 50%; }
  .availableTimes .background {
    height: 16px;
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: space-between; }
    .availableTimes .background .divider {
      width: 1px;
      height: 30%;
      position: relative;
      top: 50%;
      transform: translateY(-50%);
      background-color: #000000; }
      .availableTimes .background .divider.twelthHour {
        height: 100%; }
      .availableTimes .background .divider.sixthHour {
        height: 70%; }
      .availableTimes .background .divider.thirdthHour {
        height: 50%; }
  .availableTimes .data {
    height: 16px;
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0; }
    .availableTimes .data .dataMarker {
      width: 50%;
      height: 70%;
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      background-color: #efff5eda; }
  .availableTimes .currentTime {
    height: 16px;
    width: 1px;
    position: absolute;
    bottom: 0;
