    background-color: #ff3131; }
    .availableTimes .currentTime:before {
      width: 2px;
      height: 2px;
      position: absolute;
      top: -2px;
      left: -1px;
      transform: rotate(45deg);
      border-right: 1px solid #ff3131;
      border-bottom: 1px solid #ff3131;
      content: " "; }
    .availableTimes .currentTime:after {
      width: 2px;
      height: 2px;
      position: absolute;
      bottom: -2px;
      left: -1px;
      transform: rotate(45deg);
      border-left: 1px solid #ff3131;
      border-top: 1px solid #ff3131;
      content: " "; }

