<button on:click={toggle}>
    {#if $donateMultiple}
        Stop donating
    {:else}
        Donate multiple
    {/if}
</button>

<script>
// Stores
import { donateMultiple } from "@/js/store.js";

// Functions
function toggle() {
    $donateMultiple = !$donateMultiple;
}
</script>

