<img src={item.icon} alt="" loading="lazy" class="icon" on:click={openDetails}>
<canvas bind:this={el} width="64" height="64"></canvas>

<script>
import { onMount } from "svelte";
import { Screen } from "@/js/screen.js";
import { Particle, ParticleSystem } from "@/js/particleSystem.js";
import { Rectangle } from "@/js/objects.js";

// Components
import ItemDetails from "@/components/ItemDetails.svelte";

// Props
export var item = {};
export var open = false;

// Data
var isBirthDay = false;
var el;

// Functions
function openDetails() {
    open = true;
}

function checkBirthday(item) {
    var now = new Date();
    var day = now.getDate();
    var month = now.getMonth() + 1;

    return item.birthDate.day === day && item.birthDate.month === month;
}

function ConfettiParticle() {
    var w = Math.random() * 2 + 1;
    var h = Math.random() * 2 + 1;

    const rectangleObject = Rectangle(0, 0, w, h);
    const particleObject = Particle();

    return {
        ...rectangleObject,
        ...particleObject,
        maxAge: 50,
        tick() {
            this.y = Math.abs(this.y * 1.1);
        },
        onCreate({ w }) {
            this.rotation = Math.random() * 90;
            this.setStrokeWidth(0);

            let r = Math.random() * 255;
            let g = Math.random() * 255;
            let b = Math.random() * 255;
            this.fillStyle = `rgb(${r}, ${g}, ${b})`;

            this.x = w * Math.random();
            this.y = 1;
        }
    }
}

// On create
onMount(() => {
    if (checkBirthday(item)) {
        isBirthDay = true;

        const screen = Screen(el, 20);
        const particleSystem = ParticleSystem(0, 0);

        particleSystem.particleFunction = () => ConfettiParticle();
        particleSystem.frequency = 5;

        screen.addObject(particleSystem);
        screen.start();
    }
})
</script>

<style lang="scss">.icon {
  width: 80%;
  height: 80%;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

/*# sourceMappingURL=VillagerItem.svelte.css.map */</style>