{#if show}
    <div class="journalItem">
        <svelte:component this={typeComponent} {item} bind:open={open} />
    </div>
{/if}

{#if open}
    <ItemDetails bind:open={open} {item} />
{/if}

<script>
// Components
import ItemDetails from "@/components/ItemDetails.svelte";
import SpecimenItem from "@/components/JournalItem/SpecimenItem.svelte";
import VillgerItem from "@/components/JournalItem/VillagerItem.svelte";
import SongItem from "@/components/JournalItem/SongItem.svelte";
import FurnitureItem from "@/components/JournalItem/FurnitureItem.svelte";

// Props
export var item = {};
export var index = 0;

// Data
var open = false;
var show = false;
const typeComponentMap = {
    fish: SpecimenItem,
    bug: SpecimenItem,
    seaCreature: SpecimenItem,
    villager: VillgerItem,
    song: SongItem,
    furniture: FurnitureItem,
}

// Computed
$: itemType = item.type;
$: typeComponent = typeComponentMap[itemType];

// Functions
function openDetails() {
    open = true;
}

// On create
setTimeout(() => {
    show = true;
}, 0);
</script>

<style lang="scss">.journalItem {
  height: 100%;
  width: 100%;
  position: relative;
  box-shadow: 0 0 0 1px #000000;
  cursor: pointer; }

/*# sourceMappingURL=JournalItem.svelte.css.map */</style>