<div class="villagerPassport">
    <div class="header">
        <p>
            <span class="floater">
                <span class="line"></span> <span>Passport</span> <span class="line"></span>
            </span>
        </p>
    </div>
    <div class="mainSection">
        <img class="treeImage" src="./img/island-crop.png" alt="">
        <div class="generalInformationGrid">
            <img src={villager.image} alt="">
            <div class="textSection">
                <div class="catchPhrase">{villager.catchPhrase}</div>
                <h3>{villager.name}</h3>
                <p><b>Species:</b> {villager.species}</p>
                <p><b>Personality:</b> {villager.personality}</p>
                <p><b>Born on:</b> {villager.birthDate.string}</p>
            </div>
        </div>
    </div>
    <div class="footer">
        <p>
            <span class="right">{bottomCode}</span>
        </p>
    </div>
</div>

<script>
// Props
export var villager = {};

// Computed
$: bottomCode = (" " + villager.name.toUpperCase() + " <<").padStart(14, "<");
</script>

